.App {
  width: 100vw;
  height: 100vh;
  overscroll-behavior: none;
}

.parallax {
  /* The image used */
  background-image: url("./Images/bg_logo.png");

  /* Set a specific height */
  height: 100%;
  min-height: 1920px;
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

html {
  overscroll-behavior: none;
}
.gallery {
  height: 500px;
  width: 700px;
  padding: 50px;
}

.imagefilter {
  max-width: 650px;
  max-height: 500px;
}

.spacing {
  margin: 10px;
  padding: 30px;
}

.jumbfoot {
  padding-top: 30px;
  padding-bottom: 10px;
}

a {
  color: #ccc;
}
.nav-item {
  padding: 10px;
}

.jumbotron {
  background: #f2f2f2;
}

body {
  margin: 0;
  padding: 0;
  /* background-image: url("./Images/tracker_logo.png"); */
}

.Navbar {
  background-color: #021e39;
  width: 100%;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.background-image {
  background-size: cover;
  background-position: top;
  min-height: 100%;
  position: relative;
  size-adjust: 100%;
}

.Navbar .leftSide {
  flex: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 100%;
  float: right;
}

.Navbar .leftSide .links {
  max-height: 80px;
}

.Navbar .leftSide .links a {
  text-decoration: none;
  color: white;
  font-size: 25px;
  margin-left: 15px;
}

.Navbar .leftSide button {
  display: none;
  margin-left: 20px;
}

.Navbar .leftSide #hidden {
  display: flex;
}

.Navbar .rightSide {
  flex: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 25px;
  position: relative;
}

/*
.Navbar .rightSide .logo{
  height:60;
  width:150;
  float: right; 
  position: absolute;
}
*/

@media only screen and(max-width: 850px) {
  .Navbar .leftSide .links a {
    font-size: 20px;
  }
}

@media only screen and(max-width: 750px) {
  .Navbar .leftSide .links a {
    font-size: 15px;
  }
}

@media only screen and(max-width: 1000px) {
  .Navbar .leftSide button {
    display: flex;
  }

  .Navbar .leftSide .links {
    display: none;
  }

  .Navbar .leftSide #hidden {
    position: absolute;
    left: 0px;
    top: 80px;
    height: 230px;
    width: 100px;
    background-color: thistle;
  }

  #scheduler {
    height: 500px;
    width: 1200px;
    position: absolute;
    top: 20%;
  }
}

* {
  box-sizing: border-box;
}
.ContactUs {
  max-width: 420px;
  margin: 0 auto;
}
p {
  text-align: center;
  font-weight: 500;
}
input[type="text"],
input[type="email"],
select,
textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type="submit"] {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
input[type="submit"]:hover {
  background-color: #45a049;
}
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
